<template>
  <div class="counts-component">
    <div class="counts-btn" @click="mins"> - </div>
    <div class="counts-show">
    <input type="text" v-model="number" @keyup="inputs"/>
  </div>
    <div class="counts-btn" @click="maxs"> + </div>
  </div>
</template>
<script>
export default {
  name: 'counts',
  data () {
    return {
      number: 1
    }
  },
  /*  */
  props: {
    max: {
      type: Number,
      default: 1/*  */
    },
    min: {
      type: Number,
      default: 1
    }
  },
  methods: {
    maxs () {
      if (this.number >= this.max) { /* max */
        return
      }
      this.number++
      /*  */
      this.$emit('countss', this.number)
      this.$store.dispatch('updateOrder', ['countss', this.number])
    },
    mins () {
      if (this.number <= this.min) {
        return
      }
      this.number--
      this.$emit('countss', this.number)
      this.$store.dispatch('updateOrder', ['countss', this.number])
    },
    inputs () {
      /* \D  */
      let fix = null
      if (typeof this.number === 'string') {
        fix = Number(this.number.replace(/\D/g, ''))
        /* /g   */
      } else {
        /* 1 */
        fix = this.number
      }
      /*  */
      this.number = fix
      if (fix > this.max || fix < this.min) {
        fix = this.min/* false */
      }
      /* fixnumber */
      this.number = fix
      this.$emit('countss', this.number)
      this.$store.dispatch('updateOrder', ['countss', this.number])
    }
  }
}
</script>
<style scoped>
  .counts-component {
    position: relative;/*  */
    display: inline-block;/*  */
    overflow: hidden;
    vertical-align: middle;/*  */
  }
  .counts-show {
    float: left;
  }
  .counts-show input {
    border: none;
    border-top: 1px solid #e3e3e3;
    border-bottom: 1px solid #e3e3e3;
    height: 23px;
    line-height: 3px;
    width: 30px;
    outline: none;/*  */
    text-indent: 4px;/*  */
  }
  .counts-btn {
    border:1px solid #e3e3e3;/*  */
    float: left;
    height: 25px;
    line-height: 25px;
    width: 25px;
    text-align: center;
    cursor: pointer;/*  */
  }
  .counts-btn:hover {/*  */
    border-color: #4fc08d;
    background: #4fc08d;
    color: white;
  }
</style>



