<template>
  <div>
    <div class="dialogs" v-if="isShows" @click="closeMyself"></div>
    <!-- isShow props-->
    <!-- -->
    <div class="dialog-content" v-if="isShows">
      <div class="dialog_main">
        <!---->
        <slot name="main"></slot>
      </div>
      <div class="foot_close" @click="closeMyself"><p>X</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DiaLog',
  data() {
    return {}
  },
  props: {
    isShows: {
      type: Boolean,
      default: false /*  */
    }
  },
  methods: {
    closeMyself() {
      this.$emit('on-close')
    }
  }
}
</script>
<style scoped>
  /* //  position */
  .dialogs {
    position:absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 120%;
    background: rgba(0,0,0,0.8);
    z-index: 4800;
  }
  /* z-index*/
  /*  z-index  */
  /* z-index*/
  .dialog-content {
    width: 520px;
    position: fixed;
    left: 50%;
    top: 25%;
    border-radius: 5px;
    margin-left: -250px;
    background: white;
    z-index: 5000;
    border: 1px solid black;
  }
  .dialog_head {
    width: 56px;
    height: 43px;
    margin: 10px auto;/*  */
    justify-content: center;
    align-items: center;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  /*  */
  .dialog_main {
    background: #ffffff;
    display: flex;/*  */
    justify-content: center;
    align-content: center;
    width: 96%;
    margin: 2px auto;
     padding: 40px 0 47px 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  /*  */
  .foot_close {
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    justify-content: center;
    align-content: center;
    top: -6px;
    left: 91%;
    cursor: pointer;/*  */
  }
  /*  */
  .foot_close p {
    text-align: center;/**/
    width: 50px;
    height: 50px;
    border-radius: 50%;
    line-height: 50px;
    /* background: #fcca03;*/
    cursor: pointer;/*  */
  }
  .dialog_footer {
    background: #ffffff;
    margin: 0 10px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .dialog_footerbtn {
    background: #ffffff;
    margin: 10px 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
</style>



