<template>
  <div class="wamp">
     <div>
      <Header></Header>
    </div>
    <!-- <div class="imgage-im">
    </div> -->
    <div class="layou-login">
    <div class="actives-login">
      <h1 class="h1"></h1>
<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
  <el-form-item  prop="userName">
    <el-input type="text" placeholder="" v-model="ruleForm2.userName" auto-complete="off"></el-input>
  </el-form-item>
  <el-form-item  prop="pass">
    <el-input type="password" placeholder="" v-model="ruleForm2.pass" auto-complete="off"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
    <div class="long-register">
      
    <router-link to="/register"></router-link>
    </div>
  </el-form-item>
</el-form>
    </div></div>
  </div>
</template>
<script>
import {requestLogin} from '../api/api'
import Header from '../components/headers'
export default {
  name: 'logins',
  data() {
    let validateuserName = (rule, value, callback) => {
      let pattern = /^[\w\u4e00-\u9fa5]{3,10}$/g /* 3-10/// */
      if (value === '') {
        callback(new Error(''))
      } else if (!pattern.test(value)) {
        callback(new Error('3-10///'))
      } else {
        callback()
      }
    }
    let validatePass = (rule, value, callback) => {
      let pattern = /^\S{3,20}$/g
      if (value === '') {
        callback(new Error(''))
      } else if (!pattern.test(value)) {
        callback(new Error('3-20'))
      } else {
        callback()
      }
    }
    return {
      logining: false,
      ruleForm2: {
        userName: '',
        pass: ''
      },
      rules2: {
        userName: [
          { validator: validateuserName, trigger: 'blur' }
        ],
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ]
      }
    }
  },
  components: {
    Header
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          /* alert('submit!') */
          this.logining = true
          let loginParams = {userName: this.ruleForm2.userName, pass: this.ruleForm2.pass}
          /* loginParamsapi.jsapi.jsmockjs */
          requestLogin(loginParams).then(data => {
            this.logining = false
            let {msg, code, token} = data
            /* code200 */
            if (code === 200) {
              /* sessionStorage */
              sessionStorage.setItem('access-token', token)
              /*  */
              this.$router.push({path: '/'})
            } else {
              /*  */
              this.$message({
                type: 'error',
                message: msg
              })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },
  resetForm(formName) {
    this.$refs[formName].resetFields()
  }
}

</script>
<style scoped>
  .wamp {
    width: 100%;
    height: 100%;
    position: relative;/*  */
    font: 100% "Microsoft YaHei",tahoma,arial,'Hiragino Sans GB','\5b8b\4f53',sans-serif;
  }
  .imgage-im {
    margin: 90px auto;
    width: 1900px;
    height: 800px;
    overflow: hidden;
    background: url("../assets/login.jpg")no-repeat;/*  */
    background-size: cover;/*  */
    display: inline-block;
  }
  .layou-login {
		position: fixed;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		justify-content: center;
		align-items: center;
  }
  .actives-login {
    /* position: absolute; 
    top: 280px;
    left: 1300px;
    width: 350px;
    height: 317px;
    margin-left: -60px; */
    width: 350px;
    height: 317px;
  }
  .h1 {
     height: 45px;
     line-height: 45px;
     color: #4ab08d;
     font-size: 28px;
     font-weight: normal;
    margin-left:99px ;
    margin-bottom: 10px;
   }
  .long-register {
    float: right;
    color: #9B9EA0;
  }
</style>



