<template>
  <div>
  <div class="block">
    <!--<span class="demonstration"></span>-->
    <!-- change -->
    <el-date-picker v-model="value1" type="date" placeholder="" v-on:blur="change">
    </el-date-picker>
  </div>
 <!-- <div class="block">
    <span class="demonstration"></span>
    <el-date-picker
      v-model="value2"
      align="right"
      type="date"
      placeholder=""
      :picker-options="pickerOptions1">
    </el-date-picker>
  </div>-->
  </div>
</template>

<script>
export default {
  data() {
    return {
      pickerOptions1: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '',
          onClick(picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '',
          onClick(picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      },
      value1: '',
      value2: ''
    }
  },
  methods: {
    change: function() {
      /*  */
      this.$emit('on-change', this.value1)
    }
  }
}
</script>



