<template>
  <div class="heard-h">
    <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p>
          
          
        </p>
      </div>
      <div class="sales-board-form">
        <div class="sales-board-line">
          <div class="sales-board-line-left">
            
          </div>
          <div class="sales-board-line-right">
            <!-- dom -->
            <keep-alive><Counts @countss="getInfo('countss', $event)" :max="counts.max" :min="counts.min"></Counts></keep-alive>
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
            
          </div>
          <div class="sales-board-line-right">
            <!-- dom -->
            <Selectdown @select="getInfo('select', $event)" :SelectData="DownMen"></Selectdown>
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
            
          </div>
          <div class="sales-board-line-right">
            <!-- dom -->
            <radios @radioslist="getInfo('radioslist', $event)" :Radios="Radioss"></radios>
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
            
          </div>
          <div class="sales-board-line-right">
            {{ getprice }}
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
          </div>
          <div class="sales-board-line-right">
            <a  @click="labout" class="buy-dialog-btn"></a>
           <!-- <router-link to="/shopping"  class="buy-dialog-btn" tag="a"></router-link>-->
          </div>
        </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p></p>
        <h3></h3>
        <ul>
          <li>
            
            </li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </div>
    <MyDialog :isShow="isShowDialog" @on-close="CloseMyslef">
      <p slot="header"></p>
      <table slot="main" class="buy-dialog-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>{{ getOrders.countss }}</td>
          <td>{{ sel1 }}</td>
          <td>{{ rad2 }}</td>
          <td>{{ getprice }}</td>
        </tr>
      </table>
      <h3 slot="footer" class="buy-dialog-title"></h3>
      <div  slot="footer">
        <bank  @on-change="onChangeBanks"/>
      </div>
      <div slot="footerbtn" class="dialog-btn" @click="confirmBuy"></div>
    </MyDialog>
    <!-- dialog -->
    <my-dialog1 :isShows = "isShowCheckDialogs" @on-close="CloseMyslef">
      <p slot="main"></p>
      <div class="buy-dialog-btndialog" @click="checkStatus" slot="main">
        
      </div>
      <div class="buy-dialog-btndialog" @click="checkStatus" slot="main">
        
      </div>
    </my-dialog1>
  </div>
</template>
<script>
import Counts from './dcomponents/counts'
import Selectdown from './dcomponents/Selectdown'
import Radios from './dcomponents/Rados'
import Headers from '../../components/headers'
import Dialog from '../../components/dialog'
import Dialog1 from '../../components/dialog1'
import Bank from './dcomponents/banks'
export default {
  name: 'trait',
  data() {
    return {
      sel1: '',
      rad2: '',
      isShowDialog: false,
      isShowCheckDialogs: false,
      price: 0,
      /*  */
      countss: 1,
      select: 1,
      radioslist: 1,
      counts: {
        max: 10,
        min: 1
      },
      DownMen: [
        {
          name: '',
          value: 1
        },
        {
          name: '',
          value: 2
        },
        {
          name: '',
          value: 3
        }
      ],
      Radioss: [
        {
          title: '1',
          value: 1
        },
        {
          title: '3',
          value: 2
        },
        {
          title: '',
          value: 3
        },
        {
          title: '',
          value: 4
        }
      ]
    }
  },
  components: {
    Headers,
    Counts,
    Selectdown,
    Radios,
    MyDialog: Dialog,
    Bank,
    MyDialog1: Dialog1
  },
  methods: {
    checkStatus() {
      /* this.$http.post('/api/checkOrder', {
        orderId: this.orderId
      })
        .then((res) => {
          this.isShowSuccessDialog = true
          this.$emit('on-close-check-dialog')
        }, (err) => {
          console.log(err)
          this.$emit('on-close-check-dialog')
        }) */
      if (this.BankId !== 1 && this.BankId !== undefined) {
        /*  */
        this.$store.dispatch('updateOrder', ['countss', 1])
        alert('')
        this.isShowCheckDialogs = false
      } else {
        alert('')
        this.isShowCheckDialogs = false
      }
    },
    onChangeBanks(bankobject) {
      this.BankId = bankobject.id
    },
    getInfo(key, value) {
      /* console.log(key, value)
      console.log(this) */
      /*
      * this
      * this[key]key
      * */
      this[key] = value
      let readData = {
        'countss': this.countss, // 10
        'select': this.select, // 10
        'radioslist': this.radioslist // 20
      }
      /* console.log(readData) */
      this.price = readData.countss * 10 + readData.select * 10 + readData.radioslist * 20
    },
    labout() {
      /*  */
      this.isShowDialog = true
      if ( this.radioslist === 1) {
        this.rad2 = '1'
      } else if ( this.radioslist === 2) {
        this.rad2 = '3'
      } else if ( this.radioslist === 3) {
        this.rad2 = ''
      } else if ( this.radioslist === 4) {
        this.rad2 = ''
      }
      if ( this.select === 1) {
        this.sel1 = ''
      } else if ( this.select === 2) {
        this.sel1 = ''
      } else if ( this.select === 3) {
        this.sel1 = ''
      }
    },
    CloseMyslef() {
      /*  */
      this.isShowDialog = false
      this.isShowCheckDialogs = false
    },
    confirmBuy() {
      this.isShowCheckDialogs = true
      this.isShowDialog = false
    }
  },
  computed: {
    getprice() {
      /* store */
      this.$store.dispatch('updatePrice', this.countss * 10 + this.select * 10 + this.radioslist * 20)
      /* store */
      return this.$store.getters.getPrice
    },
    getOrders() {
      return this.$store.getters.getOrder
    }
  }
}
</script>
<style scoped>
  .dialog-btn {
    width: 60px;
    background: #4fc08d;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
    margin: 10px 390px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
  }
  .heard-h {
    width: 1240px;
    height: 100%;
    word-wrap:break-word;
    word-break:break-all;
  }
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;/*  */
  }
  .buy-dialog-btn {
    background: #4fc08d;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
  }
  .buy-dialog-btndialog {
    margin: -5px 10px;
    background: #4fc08d;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td, .buy-dialog-table th {
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #4fc08d;
    color: white;
    border: 1px solid #4fc08d;
  }
</style>



